document
    .querySelector(".request-insults")
    .addEventListener("click", function () {
        fetch("/insults")
            .then(function (res) {
                return res.json();
            })
            .then(function (data) {
                document.querySelector(".insults").innerText = data.complement;
            })
            .catch(function (err) {
                console.error(err);
            });
    });